        <el-select v-model="formSearch.xueyuan" placeholder="请选择学院" :clearable="true">
          <el-option v-for="(item, index) in xueyuanOptions" :key="index" :label="item" :value="item"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item :style='{"margin":"0 0 10px"}'>
	    <div class="lable" v-if="true" :style='{"width":"100px","padding":"0 0px","lineHeight":"42px","textAlign":"right","display":"inline-block"}'>实验室：</div>
        <el-input v-model="formSearch.shiyanshi" placeholder="实验室" @keydown.enter.native="getList(1, curFenlei)" clearable></el-input>
      </el-form-item>
	  <el-button v-if=" true " :style='{"cursor":"pointer","border":"0","padding":"0px 15px","margin":"10px 10px 0 60px","outline":"none","color":"#fff","borderRadius":"0px","background":"#1abc9e","width":"auto","fontSize":"14px","lineHeight":"40px","height":"40px"}' type="primary" @click="getList(1, curFenlei)"><i v-if="true" :style='{"color":"#fff","margin":"0 10px 0 0","fontSize":"14px"}' class="el-icon-search"></i>查询</el-button>
	  <el-button v-if="btnAuth('shiyanxiangmu','新增')" :style='{"cursor":"pointer","border":"1px solid #ddd","padding":"0px 15px","margin":"10px 10px 0 0","outline":"none","color":"#333","borderRadius":"0px","background":"#fff","width":"auto","fontSize":"14px","lineHeight":"40px","height":"40px"}' type="primary" @click="add('/index/shiyanxiangmuAdd')"><i v-if="true" :style='{"color":"#333","margin":"0 10px 0 0","fontSize":"14px"}' class="el-icon-circle-plus-outline"></i>添加</el-button>
    </el-form>
	<div class="sort_view" :style='{"padding":"0","margin":"20px 0 0","overflow":"hidden","top":"20px","textAlign":"right","background":"none","width":"calc(100% - 360px)","position":"","right":"0","float":"right","order":"5"}'>
		<el-button :style='{"border":"0","padding":"0 15px","margin":"0 5px","borderRadius":"0px"}' @click="sortClick('clicknum')">
			<span :style='{"margin":"0 2px 0 0","lineHeight":"40px","fontSize":"14px","color":"#333"}' class="icon iconfont icon-xiaoliang13" v-if="sortType!='clicknum'"></span>
			<span :style='{"margin":"0 2px 0 0","lineHeight":"40px","fontSize":"14px","color":"#333"}' class="icon iconfont icon-xiaoliang13" v-else-if="sortType=='clicknum'&&sortOrder=='desc'"></span>
			<span :style='{"margin":"0 2px 0 0","lineHeight":"40px","fontSize":"14px","color":"#333"}' class="icon iconfont icon-xiaoliang13" v-else-if="sortType=='clicknum'&&sortOrder=='asc'"></span>
			<span :style='{"color":"#333","lineHeight":"40px","fontSize":"14px"}'>点击量</span>
		</el-button>
	</div>
	<div class="list" :style='{"width":"calc(100% - 360px)","padding":"0","margin":"10px 0 0","float":"right","background":"#fff","order":"6"}'>
		
		<!-- 样式三 -->
		<div class="list3 index-pv1" :style='{"width":"100%","padding":"0 0px","flexWrap":"wrap","background":"#fff","display":"flex","height":"auto"}'>
		  <div v-for="(item, index) in dataList" :key="index" @click.stop="toDetail(item)" class="list-item animation-box">
		    <div :style='{"border":"0px solid #f6f6f6","width":"100%","padding":"0px","margin":"0 0px 0 0","overflow":"hidden","height":"200px"}' class="img-box">
				<img @click.stop="imgPreView(item.fengmian)" :style='{"width":"100%","objectFit":"cover","display":"block","clipPath":"polygon(100% 88%, 88% 100%, 0% 100%, 0% 0%, 100% 0%)","height":"100%"}' v-if="item.fengmian && item.fengmian.substr(0,4)=='http'" :src="item.fengmian" class="image" />
				<img @click.stop="imgPreView(baseUrl + (item.fengmian?item.fengmian.split(',')[0]:''))" :style='{"width":"100%","objectFit":"cover","display":"block","clipPath":"polygon(100% 88%, 88% 100%, 0% 100%, 0% 0%, 100% 0%)","height":"100%"}' v-else :src="baseUrl + (item.fengmian?item.fengmian.split(',')[0]:'')" class="image" />
			</div>
		    <div :style='{"width":"100%","padding":"0 0px","overflow":"hidden","alignItems":"center","display":"flex","height":"auto"}' class="item-info">
		      <div :style='{"width":"55%","padding":"0 0px 0 0","flex":"1"}'>
				<div class="title">{{item.shiyanmingcheng}}</div>
				<div class="title">{{item.xueyuan}}</div>
				<div v-if="item.price" class="price"><span :style='{"fontSize":"12px"}'>￥</span>{{item.price}}</div>
		        <div :style='{"padding":"2px 0px","borderColor":"#f0f0f0","borderStyle":"dashed","borderWidth":"0 0 1px 0"}' class="time">
		          <span class="icon iconfont icon-shijian21"></span>
		          <span class="text">{{item.addtime}}</span>
		        </div>
		        <div :style='{"padding":"2px 0px","borderColor":"#f0f0f0","borderStyle":"dashed","borderWidth":"0 0 1px 0"}' class="publisher">
		          <span class="icon iconfont icon-geren16"></span>
		          <span class="text">{{item.jiaoshizhanghao}}</span>
		        </div>
		        <div :style='{"width":"33%","padding":"2px 10px 0 0","borderColor":"#eee","borderStyle":"dashed","borderWidth":"0 0 1px 0","display":"inline-block"}' class="collect">
		          <span class="icon iconfont icon-shoucang10"></span>
		          <span class="text">{{item.storeupnum}}</span>
		        </div>
		        <div :style='{"width":"34%","padding":"2px 10px 0 0","borderColor":"#eee","borderStyle":"dashed","borderWidth":"0 0 1px 0","display":"inline-block"}' class="view">
		          <span class="icon iconfont icon-chakan9"></span>
		          <span class="text">{{item.clicknum}}</span>
		        </div>
		      </div>
		      <div class="desc">{{item.shiyanyaoqiu}}</div>
		    </div>
		  </div>
		</div>
	</div>

	<!-- 热门信息 -->
	<div class="hot" :style='{"padding":"0","margin":"0","background":"#fff","width":"300px","float":"left","height":"auto","order":"5"}'>
	  <div :style='{"width":"100%","padding":"20px 0px","fontSize":"28px","color":"#1abc9e","textAlign":"center","background":"url(http://codegen.caihongy.cn/20230915/b96d1e15768f4ac495e60a79e04cb731.png) no-repeat center bottom"}'>热门信息</div>
	  <div :style='{"padding":"30px 10px","flexWrap":"wrap","background":"#fff","display":"flex","width":"100%","justifyContent":"space-between","height":"auto"}'>
	    <div v-for="item in hotList" :key="item.id" :style='{"cursor":"pointer","width":"48%","margin":"0 0 20px","background":"#fff","height":"auto"}' @click="toDetail(item)">
	      <img :style='{"width":"100%","objectFit":"cover","display":"block","clipPath":"polygon(100% 88%, 88% 100%, 0% 100%, 0% 0%, 100% 0%)","height":"150px"}' v-if="item.fengmian && item.fengmian.substr(0,4)=='http'" :src="item.fengmian" alt="">
	      <img :style='{"width":"100%","objectFit":"cover","display":"block","clipPath":"polygon(100% 88%, 88% 100%, 0% 100%, 0% 0%, 100% 0%)","height":"150px"}' v-else :src="baseUrl + (item.fengmian?item.fengmian.split(',')[0]:'')" alt="">
	      <div :style='{"padding":"0 10px","borderColor":"#eee","overflow":"hidden","whiteSpace":"nowrap","color":"#666","textAlign":"center","borderWidth":"0 0 1px","width":"100%","lineHeight":"32px","fontSize":"14px","textOverflow":"ellipsis","borderStyle":"solid"}'>{{item.shiyanmingcheng}}</div>
	      <div :style='{"padding":"0 10px","borderColor":"#eee","overflow":"hidden","whiteSpace":"nowrap","color":"#666","textAlign":"center","borderWidth":"0 0 1px","width":"100%","lineHeight":"32px","fontSize":"14px","textOverflow":"ellipsis","borderStyle":"solid"}'>{{item.xueyuan}}</div>
	      <!--<div :style='{"padding":"0 10px","lineHeight":"32px","fontSize":"14px","color":"#999","textAlign":"right"}'>2022-02-02</div>-->
	    </div>
	  </div>
	</div>
	
	<el-pagination
	  background
	  id="pagination"
	  class="pagination"
	  :pager-count="7"
	  :page-size="pageSize"
	  :page-sizes="pageSizes"
	  prev-text="<"
	  next-text=">"
	  :hide-on-single-page="true"
	  :layout='["total","prev","pager","next","sizes","jumper"].join()'
	  :total="total"
	  :style='{"padding":"5px 7%","margin":"20px auto","color":"#333","textAlign":"center","width":"100%","clear":"both","lineHeight":"40px","fontWeight":"500","height":"40px"}'
	  @current-change="curChange"
      @size-change="sizeChange"
	  @prev-click="prevClick"
	  @next-click="nextClick"
	></el-pagination>

  </div>
  <el-dialog title="预览图" :visible.sync="previewVisible" width="50%">
  	<img :src="previewImg" alt="" style="width: 100%;">
  </el-dialog>
