</template>

<script>
  export default {
    //数据集合
    data() {
      return {
	    layouts: '',
		swiperIndex: -1,
        baseUrl: '',
        breadcrumbItem: [
          {
            name: '实验项目'
          }
        ],
        formSearch: {
          shiyanmingcheng: '',
          kechengmingcheng: '',
          xueyuan: '',
          shiyanshi: '',
        },
        fenlei: [],
		feileiColumn: '',
        hotList: [],
        dataList: [],
        total: 1,
        pageSize: 6,
		pageSizes: [10,20,30,50],
        totalPage: 1,
        curFenlei: '全部',
        isPlain: false,
        indexQueryCondition: '',
	      kechengmingchengOptions: [],
	      xueyuanOptions: [],
        timeRange: [],
		centerType:false,
		previewImg: '',
		previewVisible: false,
		sortType: 'id',
		sortOrder: 'desc',
      }
    },
    created() {
		if(this.$route.query.centerType){
			this.centerType = true
		}
		this.indexQueryCondition = this.$route.query.indexQueryCondition ? this.$route.query.indexQueryCondition : '';
		this.baseUrl = this.$config.baseUrl;
		this.$http.get('option/kechengxinxi/kechengmingcheng').then(res => {
			if (res.data.code == 0) {
				this.kechengmingchengOptions = res.data.data;
			}
		});
		this.$http.get('option/xueyuanxinxi/xueyuan').then(res => {
			if (res.data.code == 0) {
				this.xueyuanOptions = res.data.data;
			}
		});
      this.getFenlei();
      this.getList(1, '全部');
      this.getHotList();
    },
    //方法集合
    methods: {
		add(path) {
			let query = {}
			if(this.centerType){
				query.centerType = 1
			}
			this.$router.push({path: path,query:query});
		},
      getHotList() {
        let autoSortUrl = "";
        autoSortUrl = "shiyanxiangmu/autoSort";
          this.$http.get(autoSortUrl, {params: {
              sfsh: '是',
              page: 1,
              limit: 4,
          }}).then(res => {
              if (res.data.code == 0) {
                  this.hotList = res.data.data.list;
              }
          })
      },
      getFenlei() {
		this.$http.get('xueyuanxinxi/lists').then(res => {
		  if (res.data.code == 0) {
		    this.fenlei = res.data.data;
		  }
		});
		this.feileiColumn = 'xueyuan'
      },
      getList(page, fenlei, ref = '') {
		if(fenlei == '全部') this.swiperIndex = -1;
		for(let i=0;i<this.fenlei.length;i++) {
			if(fenlei == this.fenlei[i][this.feileiColumn]) {
				this.swiperIndex = i;
				break;
			}
		}
		if(fenlei){
			this.curFenlei = fenlei;
		}
        // if (this.curFenlei == '全部') {
        //   this.isPlain = false;
        // } else {
        //   this.isPlain = true;
        // }
        let params = {page, limit: this.pageSize};
        let searchWhere = {};
        if (this.formSearch.shiyanmingcheng != '') searchWhere.shiyanmingcheng = '%' + this.formSearch.shiyanmingcheng + '%';
        if (this.formSearch.kechengmingcheng != '') searchWhere.kechengmingcheng = this.formSearch.kechengmingcheng;
        if (this.formSearch.xueyuan != '') searchWhere.xueyuan = this.formSearch.xueyuan;
        if (this.formSearch.shiyanshi != '') searchWhere.shiyanshi = '%' + this.formSearch.shiyanshi + '%';
		if(!this.centerType){
			params['sfsh'] = '是';
		}
        if (this.curFenlei != '全部') searchWhere.xueyuan = this.curFenlei;
		if (this.sortType) searchWhere.sort = this.sortType
		if (this.sortOrder) searchWhere.order = this.sortOrder
        this.$http.get(`shiyanxiangmu/${this.centerType?'page':'list'}`, {params: Object.assign(params, searchWhere)}).then(res => {
          if (res.data.code == 0) {
            this.dataList = res.data.data.list;
            this.total = Number(res.data.data.total);
            this.pageSize = Number(res.data.data.pageSize);
            this.totalPage = res.data.data.totalPage;
			
			this.pageSizes = [this.pageSize, this.pageSize*2, this.pageSize*3, this.pageSize*5];
          }
        });
      },
	  sortClick(type){
		  if(this.sortType==type){
			  if(this.sortOrder == 'desc'){
				  this.sortOrder = 'asc'
			  }else{
				  this.sortOrder = 'desc'
			  }
		  }else{
			  this.sortType = type
			  this.sortOrder = 'desc'
		  }
		  this.getList(1, '全部')
	  },
      curChange(page) {
        this.getList(page,this.curFenlei);
      },
      prevClick(page) {
        this.getList(page,this.curFenlei);
      },
      sizeChange(size){
        this.pageSize = size
        this.getList(1,this.curFenlei);
      },
      nextClick(page) {
        this.getList(page,this.curFenlei);
      },
	  imgPreView(url){
		  this.previewImg = url
		  this.previewVisible = true
	  },
      toDetail(item) {
		  let params = {
			  id: item.id
		  }
		  if(this.centerType){
			  params.centerType = 1
		  }
        this.$router.push({path: '/index/shiyanxiangmuDetail', query: params});
      },
	btnAuth(tableName,key){
		if(this.centerType){
			return this.isBackAuth(tableName,key)
		}else{
			return this.isAuth(tableName,key)
		}
	},
	backClick() {
		this.$router.push({path: '/index/center'});
	},
    }
  }
</script>

